import React, { useEffect } from "react";
import { ProgressBar, Alert } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import ReactReadMoreReadLess from "react-read-more-read-less";
import { Link } from "react-router-dom";
import ReactImageMagnify from "react-image-magnify";
import InnerImageZoom from "react-inner-image-zoom";
import "react-inner-image-zoom/lib/InnerImageZoom/styles.css";
import { newsfeedlist, newsfeeddelete } from "../../reduxx/actions/actions";

function Listnewsfeed() {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(newsfeedlist());
  }, []);
  const newsfeed = useSelector(({ newsfeedlist }) => newsfeedlist.payload);

  return (
    <div>
      <div className="row">
        <div className="col-xl-12 grid-margin stretch-card">
          <div className="card">
            <div className="card-body">
              <h4 className="card-title">News Feed</h4>
              {Array.isArray(newsfeed) ? (
                newsfeed.map((news) => {
                  return (
                    <div className="row" style={{ marginBottom: "30px" }}>
                      <div className="col-lg-3">
                        {
                          // sample image
                        }
                        <InnerImageZoom src={news.photo} />
                      </div>
                      <div className="col-lg-9">
                        {
                          // title
                        }
                        <h3>
                          {
                            // title
                          }
                          {news.title}
                        </h3>
                        <ReactReadMoreReadLess
                          charLimit={400}
                          readMoreText={"Read more ▼"}
                          readLessText={"Read less ▲"}
                        >
                          {news.description}
                        </ReactReadMoreReadLess>
                        <p style={{ marginTop: "20px" }}>
                          {
                            // taluk badge
                          }
                          <span className="badge badge-success">
                            {news.taluk}
                          </span>{" "}
                          &nbsp;
                          {
                            // district badge
                          }
                          <span className="badge badge-success">
                            {news.district}
                          </span>
                        </p>

                        {
                          // row
                        }
                        {
                          // space between
                        }
                        <div className="row">
                          <div className="col-md-6">
                            {
                              // view button
                            }
                            <Link
                              to={"/Newsfeed/UpdateNewsfeed/" + news.id}
                              className="btn btn-primary"
                            >
                              View
                            </Link>
                          </div>
                          <div
                            className="col-md-6"
                            style={{
                              display: "flex",
                              justifyContent: "flex-end",
                            }}
                          >
                            {
                              // delete button and end of row
                            }
                            <a
                              onClick={() => {
                                dispatch(newsfeeddelete({ id: news.id + "" }));
                              }}
                              className="btn btn-danger"
                            >
                              Delete
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })
              ) : (
                <Alert
                  variant="primary"
                  style={{
                    fontWeight: "bold",
                    padding: "20px",
                    textAlign: "center",
                  }}
                >
                  No NewsFeeds Found!
                </Alert>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Listnewsfeed;
